<template>
  <div id="fabrics-upload-component">
    <div class="card">
      <div class="item">
        <p class="label">Photos<span class="red-point">*</span></p>
        <div class="main-photo-container">
          <div class="note-container">
            <div class="note">
              <p>File Name: cloth number + _ + name</p>
              <p>Photo within 1MB</p>
              <p>1) Fabric picture: 10x10cm / 300dpi</p>
              <p>2) Large image: 2000pixel / 300dpi</p>
            </div>
          </div>
        </div>
        <div class="photo-container">
          <div class="icon-upload" @click="onPhotosUpload">
            <img :src="iconUploadUrl" alt="upload" />
          </div>
          <div class="photos">
            <div
              v-for="(base64, index) in photosBase64"
              :key="index"
              class="photo"
            >
              <el-tooltip :content="base64.name" placement="top" effect="light">
                <img :src="base64.src" :alt="base64.name" />
              </el-tooltip>
              <font-awesome-icon
                class="icon"
                :icon="['far', 'times-circle']"
                @click="onPhotoRemove(index)"
              ></font-awesome-icon>
            </div>
          </div>
        </div>
      </div>
      <div class="item">
        <p class="label">Video Url</p>
        <el-input
          v-for="(url, index) in form.video_urls"
          :key="index"
          class="value"
          :style="{ marginBottom: '8px' }"
          type="text"
          :model-value="url"
          @input="value => onVideoUrlChange(index, value)"
        ></el-input>

        <div class="icon-add" @click="onVideoUrlAdd">
          <font-awesome-icon
            class="icon"
            :icon="['fas', 'plus-circle']"
          ></font-awesome-icon>
        </div>
      </div>
      <div class="or-container">
        <div class="item">
          <p class="label">New Art No in SAP (擇一)</p>
          <el-input
            class="value"
            :class="{
              success: sapArtNoInputStatus === 1,
              error: sapArtNoInputStatus === 2
            }"
            type="text"
            v-model="form.sap_art_no"
            :disabled="isSapArtNoDisabled"
          ></el-input>
        </div>
        <p class="or">Or</p>
        <div class="item">
          <p class="label">Art No (擇一)</p>
          <el-input
            class="value"
            :class="{
              success: artNoInputStatus === 1,
              error: artNoInputStatus === 2
            }"
            type="text"
            v-model="form.art_no"
            :disabled="isArtNoDisabled"
          ></el-input>
        </div>
      </div>
      <div class="line"></div>
      <div class="item">
        <p class="label">Name<span class="red-point">*</span></p>
        <el-autocomplete
          class="value"
          :model-value="form.name"
          :fetch-suggestions="onQuerySearch"
          @input="value => (form.name = toNameUpperCase(value))"
        ></el-autocomplete>
      </div>
      <div class="item">
        <p class="label">Real Composition<span class="red-point">*</span></p>
        <el-autocomplete
          class="value"
          :model-value="form.real_composition"
          :fetch-suggestions="onQuerySearchComposition"
          @input="value => (form.real_composition = toNameUpperCase(value))"
        ></el-autocomplete>
      </div>
      <div class="item">
        <p class="label">Usable Width<span class="red-point">*</span></p>
        <div class="inch-item">
          <el-input class="value" type="number" v-model="form.width"></el-input>
          <p>Inch</p>
        </div>
      </div>
      <div class="item">
        <p class="label">Weight in g/m²<span class="red-point">*</span></p>
        <el-input class="value" type="number" v-model="form.weight"></el-input>
      </div>
      <div class="item">
        <p class="label">Status<span class="red-point">*</span></p>
        <el-select class="value" v-model="form.status">
          <el-option
            v-for="(item, index) in statusOptions"
            :key="index"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <p class="label">Limited Brand Name</p>
        <el-select
          :disabled="form.status !== 'exclusive'"
          class="value"
          v-model="form.exclusive_for"
        >
          <el-option
            v-for="(item, index) in brandOptions"
            :key="index"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <p class="label">
          Family Fabrics (art_no) (use "," to separate)
        </p>
        <el-input
          class="value"
          type="text"
          v-model="form.family_fabric_art_no"
        ></el-input>
      </div>
      <div class="item">
        <p class="label">
          Fabric Works - SKU number (use "," to separate)
        </p>
        <el-input
          class="value"
          type="text"
          v-model="form.fabric_works_skus"
        ></el-input>
      </div>
      <div class="line"></div>
      <div class="item">
        <p class="label">Tags Selector</p>
        <div class="tag-wrapper">
          <el-select v-model="tagType" placeholder="Tag Type...">
            <el-option
              v-for="item in tagTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <el-autocomplete
          class="input"
          v-model="fabricTypeValue"
          :fetch-suggestions="onQuerySearchFabricType"
          :disabled="!tagType"
          @keydown.enter="onSelectFabricType({ value: fabricTypeValue })"
          @select="onSelectFabricType"
        ></el-autocomplete>
      </div>
      <div class="item">
        <p class="label">Tags</p>
        <div class="tag-wrapper">
          <el-tag
            v-for="(tag, index) in form.tags"
            :key="index"
            class="tag"
            closable
            :type="getTagType(tag.tag_type)"
            @close="onTagClose(tag)"
          >
            {{ tag.tag_name }}
          </el-tag>
        </div>
      </div>

      <div class="footer-container">
        <el-button type="danger" @click="visible = true">Delete</el-button>
        <el-button type="info" @click="onCancel">Cancel</el-button>
        <el-button type="success" @click="onSave">Update</el-button>
      </div>
    </div>

    <el-dialog v-model="visible" width="600px" top="45px">
      <h1 class="dialog-title">Delete {{ form.name }}</h1>
      <div class="dialog-button-wrapper">
        <el-button type="danger" @click="onDelete">
          Delete
        </el-button>
        <el-button type="info" @click="visible = false">
          Cancel
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, reactive, onMounted, watch } from "vue";
import { getLastViewTimeString, loading } from "@/utils";
import { useRouter } from "vue-router";
import iconUploadUrl from "@/assets/images/icons/png/upload.png";
import {
  checkArtNo,
  getFabric,
  getFabricCompositionList,
  getFabricNameList,
  postDeleteFabtic,
  postUpdateFabric
} from "@/apis/fabric";
import { OptionItem } from "@/typings/asideFilter";
import { getMemberTeams } from "@/apis/member";
import { getFabricTags } from "@/apis/fabric";
import { MemberTeam } from "@/typings/member";
import {
  FabricFileResponse,
  FabricTag,
  FabricTagResponse,
  FabricType,
  PostUploadFabricParams
} from "@/typings/fabric";
import { toNameUpperCase } from "@/utils/fabric";
import { ElNotification } from "element-plus";

const statusOptions: OptionItem[] = [
  { label: "Public", value: "public", checked: false },
  { label: "Private", value: "private", checked: false },
  { label: "Exclusive - Brand", value: "exclusive", checked: false }
];
const tagTypeOptions: OptionItem[] = [
  { label: "Sport", value: "sport", checked: false },
  { label: "Style", value: "style", checked: false },
  { label: "Clothes", value: "clothes", checked: false },
  { label: "Category", value: "category", checked: false }
];

const getTagType = (type: string) => {
  switch (type) {
    case "sport":
      return "";
    case "style":
      return "success";
    case "clothes":
      return "info";
    case "category":
      return "warning";
    default:
      return "danger";
  }
};

interface Base64 {
  type: string;
  name: string;
  src: string;
}

const Component = defineComponent({
  name: "FabricEdit",
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const router = useRouter();
    const { id } = toRefs(props);
    const form = reactive<PostUploadFabricParams>({
      main_photo: null,
      photos: [],
      photos_with_id: [],
      video_urls: [],
      sap_art_no: "",
      art_no: "",
      name: "",
      real_composition: "",
      width: 0,
      weight: 0,
      status: "public",
      exclusive_for: null,
      family_fabric_art_no: "",
      fabric_works_skus: "",
      tags: []
    });
    const visible = ref(false);
    const storeMainPhoto = ref<File | null>(null);
    const storePhotos = ref<File[]>([]);
    const storePhotosWithId = ref<FabricFileResponse[]>([]);
    const tagType = ref<string | null>(null);
    const fabricTypeValue = ref("");
    const fabricTypes = ref<string[]>([]);
    const isSapArtNoDisabled = ref(false);
    const isArtNoDisabled = ref(false);
    const timeId = ref<ReturnType<typeof setTimeout> | undefined>(undefined);
    const sapArtNoInputStatus = ref(0);
    const artNoInputStatus = ref(0);

    const mainPhotoBase64 = ref("");
    const photosBase64 = ref<Base64[]>([]);
    const nameList = ref<string[]>([]);
    const compositionList = ref<string[]>([]);
    const brandOptions = ref<OptionItem[]>([]);

    watch([storeMainPhoto], () => {
      if (!storeMainPhoto.value) return {};
      const reader = new FileReader();
      reader.onload = function(e: any) {
        mainPhotoBase64.value = e.target.result;
      };
      reader.readAsDataURL(storeMainPhoto.value);
      form.main_photo = storeMainPhoto.value;
    });

    watch([storePhotos], () => {
      form.photos = storePhotos.value;
      if (!storePhotos.value.length) return;
      for (let i = 0; i < storePhotos.value.length; i++) {
        const reader = new FileReader();
        reader.onload = function(e: any) {
          photosBase64.value = photosBase64.value.concat({
            type: "photo",
            name: storePhotos.value[i].name,
            src: e.target.result
          });
        };
        reader.readAsDataURL(storePhotos.value[i]);
      }
    });

    watch([storePhotosWithId], () => {
      form.photos_with_id = storePhotosWithId.value;
      if (!storePhotosWithId.value.length) return;
      for (let i = 0; i < storePhotosWithId.value.length; i++) {
        photosBase64.value = photosBase64.value.concat({
          type: "photo_with_id",
          name: storePhotosWithId.value[i].name,
          src: storePhotosWithId.value[i].file
        });
      }
    });

    watch([tagType], async () => {
      if (tagType.value) {
        const { data } = await getFabricTags(tagType.value);
        fabricTypes.value = data.map((it: FabricType) => it.name);
      }
    });

    watch(
      () => form.sap_art_no,
      () => {
        timeId.value && clearTimeout(timeId.value);
        timeId.value = setTimeout(async () => {
          if (!form.sap_art_no) {
            sapArtNoInputStatus.value = 0;
            return;
          }
          const { data } = await checkArtNo({ sap_art_no: form.sap_art_no });
          sapArtNoInputStatus.value = data.valid ? 1 : 2;
        }, 500);
      }
    );

    watch(
      () => form.art_no,
      () => {
        timeId.value && clearTimeout(timeId.value);
        timeId.value = setTimeout(async () => {
          if (!form.art_no) {
            artNoInputStatus.value = 0;
            return;
          }
          const { data } = await checkArtNo({ art_no: form.art_no });
          artNoInputStatus.value = data.valid ? 1 : 2;
        }, 500);
      }
    );

    const init = async () => {
      const { data: detail } = await getFabric(id.value);
      const { data: names } = await getFabricNameList();
      const { data: compositions } = await getFabricCompositionList();
      const { data: teams } = await getMemberTeams();
      storePhotosWithId.value = detail.media;
      form.video_urls = detail.video_urls;
      form.sap_art_no = detail.sap_art_no;
      form.art_no = detail.art_no;
      form.name = detail.name;
      form.real_composition = detail.real_composition;
      form.width = detail.width;
      form.weight = detail.weight;
      form.status = detail.status;
      form.exclusive_for = detail.exclusive_for;
      form.family_fabric_art_no = detail.family_fabric_art_no;
      form.fabric_works_skus = detail.fabric_works_skus;
      form.tags = detail.tags.map((it: FabricTagResponse) => ({
        tag_type: it.tag_type,
        tag_name: it.name
      }));
      isSapArtNoDisabled.value = !!form.sap_art_no;
      isArtNoDisabled.value = !!form.art_no;

      nameList.value = names.names.map((it: string[]) => it[0]);
      compositionList.value = compositions.real_composition.map(
        (it: string[]) => it[0]
      );
      brandOptions.value = teams.map((it: MemberTeam) => ({
        label: it.name,
        value: it.id,
        checked: false
      }));
    };

    onMounted(init);

    const createFilter = (queryString: string) => {
      return (option: string) => {
        return option.toLowerCase().indexOf(queryString.toLowerCase()) > -1;
      };
    };

    const onQuerySearch = async (queryString: string, cb: Function) => {
      const results = queryString
        ? nameList.value.filter(createFilter(queryString))
        : nameList.value;
      cb(results.map((text: string) => ({ value: text })));
    };

    const onQuerySearchComposition = async (
      queryString: string,
      cb: Function
    ) => {
      const results = queryString
        ? compositionList.value.filter(createFilter(queryString))
        : compositionList.value;
      cb(results.map((text: string) => ({ value: text })));
    };

    const onQuerySearchFabricType = async (
      queryString: string,
      cb: Function
    ) => {
      const results = queryString
        ? fabricTypes.value.filter(createFilter(queryString))
        : fabricTypes.value;
      cb(results.map((text: string) => ({ value: text })));
    };

    const onMainPhotoUpload = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.addEventListener("change", async () => {
        if (element.files) {
          storeMainPhoto.value = element.files[0];
        }
      });
      element.click();
    };

    const onSelectFabricType = (data: { value: string }) => {
      if (!fabricTypeValue.value) return;
      if (
        form.tags &&
        form.tags.some(
          (it: FabricTag) =>
            it.tag_name === data.value && it.tag_type === tagType.value
        )
      ) {
        fabricTypeValue.value = "";
        return;
      }
      form.tags = [
        ...form.tags!,
        { tag_name: data.value, tag_type: tagType.value! }
      ];
      fabricTypeValue.value = "";
    };

    const onTagClose = (data: FabricTag) => {
      console.log(data);
      form.tags = form.tags.filter(
        (it: FabricTag) =>
          it.tag_name !== data.tag_name && it.tag_type !== data.tag_type
      );
    };

    const onPhotosUpload = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.multiple = true;
      element.addEventListener("change", async () => {
        if (element.files) {
          let ary: File[] = [];
          for (let i = 0; i < element.files.length; i++) {
            ary = [...ary, element.files[i]];
          }
          storePhotos.value = ary;
        }
      });
      element.click();
    };

    const onPhotoRemove = (index: number) => {
      const target: Base64 = photosBase64.value[index];
      photosBase64.value = [];
      if (target.type === "photo") {
        storePhotos.value = storePhotos.value.filter(
          (it: File) => it.name !== target.name
        );
        if (!storePhotosWithId.value.length) return;
        for (let i = 0; i < storePhotosWithId.value.length; i++) {
          photosBase64.value = photosBase64.value.concat({
            type: "photo_with_id",
            name: storePhotosWithId.value[i].name,
            src: storePhotosWithId.value[i].file
          });
        }
      }
      if (target.type === "photo_with_id") {
        storePhotosWithId.value = storePhotosWithId.value.filter(
          (it: FabricFileResponse) => it.name !== target.name
        );
        if (!storePhotos.value.length) return;
        for (let i = 0; i < storePhotos.value.length; i++) {
          const reader = new FileReader();
          reader.onload = function(e: any) {
            photosBase64.value = photosBase64.value.concat({
              type: "photo",
              name: storePhotos.value[i].name,
              src: e.target.result
            });
          };
          reader.readAsDataURL(storePhotos.value[i]);
        }
      }
    };

    const onVideoUrlAdd = () => {
      if (!form.video_urls) return;
      form.video_urls = [...form.video_urls, ""];
    };

    const onVideoUrlChange = (index: number, value: string) => {
      // @ts-ignore
      form.video_urls[index] = value;
    };

    const onDelete = async () => {
      const loadingInstance = loading();
      try {
        await postDeleteFabtic(id.value);
        ElNotification({
          title: "Success",
          type: "success",
          message: `Fabric ${form.name} is removed.`
        });
        visible.value = false;
        await router.push({ name: "Fabrics" });
      } finally {
        loadingInstance.close();
      }
    };

    const onCancel = async () => {
      await router.back();
    };

    const onSave = async () => {
      const loadingInstance = loading();
      try {
        const { data } = await postUpdateFabric(id.value, form);
        await router.push({ name: "FabricDetail", params: { id: data.id } });
      } finally {
        loadingInstance.close();
      }
    };

    return {
      form,
      visible,
      mainPhotoBase64,
      photosBase64,
      tagType,
      fabricTypeValue,

      isSapArtNoDisabled,
      isArtNoDisabled,
      sapArtNoInputStatus,
      artNoInputStatus,
      statusOptions,
      brandOptions,
      tagTypeOptions,

      onQuerySearch,
      onQuerySearchComposition,
      onQuerySearchFabricType,
      onSelectFabricType,
      onTagClose,
      onMainPhotoUpload,
      onPhotosUpload,
      onPhotoRemove,
      onVideoUrlAdd,
      onVideoUrlChange,
      onDelete,
      onCancel,
      onSave,

      toNameUpperCase,
      getTagType,
      getLastViewTimeString,
      iconUploadUrl
    };
  }
});

export default Component;
</script>

