export function toNameUpperCase(name: string) {
  const splitRules = [" ", "/", "+", "%"];
  const splitAry: string[] = name
    .split("")
    .reduce((ary: string[], letter: string, index: number) => {
      if (index === 0) {
        ary.push(letter);
        return ary;
      }
      if (splitRules.includes(letter)) {
        ary.push(letter);
      } else {
        ary[ary.length - 1] = `${ary[ary.length - 1] + letter}`;
      }
      return ary;
    }, []);

  return splitAry
    .map((it: string, index: number) => {
      return it
        .split("")
        .map((letter: string, _index: number) => {
          return _index === (index === 0 ? 0 : 1)
            ? letter.toUpperCase()
            : letter.toLowerCase();
        })
        .join("");
    })
    .join("");
}
